<template>
    <div id="new">
        <Header2  :FlagSignin=false :FlagAcc=false :FlagEdit=false :FlagAdded=false :FlagNew=true />
       <div class="content">
           <div class="con">
               <input type="text" placeholder="请写入小区/写字楼/学校等" v-model="arr" />
               <button @click="click()">提交</button>
           </div>
           <div class="conn">为了满足商家的送餐要求，建议您从列表中选择地址</div>
           <div class="connn">
               <div>找不到地址？</div>
               <div>请尝试输入小区、写字楼或学校名</div>
               <div>详细地址（如门牌号）可稍后输入哦。</div>
           </div>
           <div class="cont">
               <div class="cont1" v-for="(item,index) in list" :key="index" @click="add(item)" >
                   <div class="cont2">{{item.address}}</div>
                   <div class="cont3">{{item.name}}</div>
               </div>
           </div>
       </div>
        
        
    </div>
</template>

<script>
import Header2 from '../components/Header2' 
export default {
  components:{
        Header2
    }, 
  name: 'HelloWorld',
    filters: {
    ellipsis (value) {
        if (!value) return ''
        if (value.length > 15) {
            return value.slice(0,15) + '...'
        }
        return value
        }
    },
  data () {
    return {
      arr:'',
     list:[]
      
    }
  },
  created(){
      
  },
  methods:{
    click(){
          this.axios.get(`http://elm.cangdu.org/v1/pois?type=nearby&keyword=${this.arr}`).then(res=>{
              console.log(res.data)
            this.list=res.data;
          })
    },
    add(item){
        this.$router.push(`/Added?name=${item.name}`)
    }
  },
  mounted(){
    
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
#new{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: #F4F5F9;
    position: fixed;
    top: 0;
    background: #fff;
    z-index:5;
    .content{
        width: 100%;
        flex: 1;
        overflow: auto;
        position: relative;
        margin-top: 1.8rem;
        position: relative;
        .con{
            width: 100%;
            background: white;
            height: 3rem;
            input{
                width: 65%;
                height: 1.5rem;
                border-radius:5px; 
                background: #f2f2f2;
                border: 0.01rem solid gray;
                margin-top: 0.75rem;
                margin-left: 5%;
                padding-left: 5%;
            }
            button{
                background: #3199e8;
                width: 20%;
                height: 1.5rem;
                border-radius:5px; 
                border: none;
                color: white;
            }
        }
        .conn{
            width: 95%;
            height: 1.3rem;
            line-height: 1.3rem;
            color: #ff883f;
            background: #fff6e4;
            padding-left: 5%
        }
        .connn{
            color: #969696;
            margin-top: 6rem;
            width: 100%;
            text-align: center;
            font-size: 0.7rem;
            div{
                margin-top: 0.5rem;
            }
        }
        .cont{
            background: #f5f5f5;
            position: absolute;
            z-index: 5;
            top: 4.5rem;
            width: 100%;
            .cont1{
                width:100%;
                color: #969696;
                border-bottom: 0.01rem solid gainsboro;
               
                .cont2{
                    margin-top: 0.5rem;
                    padding-left: 5%;
                    width: 90%;
                }
                .cont3{
                    width: 90%;
                    margin-top: 0.5rem;
                    margin-bottom: 0.5rem;
                    padding-left: 5%;
                }
            }
        }
    }
    
}
    
</style>



